interface Coins {
  coin500: number;
  coin100: number;
  coin50: number;
  coin10: number;
}

export class CoinVault {
  private coinsQuantity: Coins;
  private coinsPrice: Coins;

  constructor() {
    this.coinsQuantity = {
      coin500: 0,
      coin100: 0,
      coin50: 0,
      coin10: 0,
    };

    this.coinsPrice = {
      coin500: 500,
      coin100: 100,
      coin50: 50,
      coin10: 10,
    };
  }

  addCoins(coins: Coins) {
    [...Object.entries(coins)].forEach(([key, value]) => {
      this.coinsQuantity[key] += value;
    });
  }

  getCoins(): Coins {
    return this.coinsQuantity;
  }

  getBalance() {
    return [...Object.entries(this.coinsQuantity)].reduce(
      (previous, [key, value]) => previous + this.coinsPrice[key] * value,
      0
    );
  }

  chargeMoney(money: number) {
    try {
      this.validateMoney(money);
      this.addCoins(this.generateRandomCoins(money));
    } catch (err) {
      throw err;
    }
  }

  validateMoney(money: number): boolean {
    if (money + this.getBalance() > 100000) {
      throw new Error('돈통이 가득찼어요! 100,000원 까지만 보관 가능합니다.');
    }
    if (money % 10 !== 0) {
      throw new Error('상평통보는 안 받습니다. 10원단위로 넣어주세요!');
    }
    return;
  }

  generateRandomCoins(money: number): Coins {
    let balance = money;
    const generatedCoins = {
      coin500: 0,
      coin100: 0,
      coin50: 0,
      coin10: 0,
    };

    //TODO

    //uitls -> CoinObject Arr를 주는 함수
    [...Object.entries(this.coinsPrice)].forEach(([key, price]) => {
      //object(coin종류)를 순회한다
      const maxQuotient = balance / price; //coin종류에 맞는 최대 몫을 정한다

      if (price === 10) {
        //10원(마지막 요소)일때
        generatedCoins[key] = maxQuotient; //남은걸 다 때려박는다
        return;
      }

      //마지막 요소가 아닐때
      //범위 내에서 랜덤하게 정수로 뽑아주는 함수
      const randomQuantity = Math.floor(Math.random() * maxQuotient); //최대몫 미만으로 랜덤하게 뽑느다.
      balance -= price * randomQuantity; //뽑은만큼 잔액을 차감한다.
      generatedCoins[key] = randomQuantity; //뽑은 수량을 저장한다.
    });

    return generatedCoins;
  }
}
